/* For Mobile */
@media screen and (max-width: 540px) {
  .map {
    width: 100%;
    height: 40vh;
  }
}

@media screen and (min-width: 540px) {
  .map {
    width: 100%;
    height: 80vh;
  }
}

.scrolling-wrapper {
  margin: 0 auto;
  width:100vw;
  height:100vh;
  overflow: hidden;
  position: relative;
}